<template>
  <form
    @submit.prevent="submit"
    @click.stop
    class="tag-form"
  >
    <input
      class="tag-form__input"
      type="text"
      placeholder="#tag hinzufügen"
      v-model="name"
    >
  </form>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  props: {
    collection: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      name: ''
    }
  },
  methods: {
    ...mapActions ({
      addTag: 'tag/add'
    }),
    submit () {
      this.addTag({
        collectionId: this.collection.id,
        name: this.name
      })

      this.name = ''
    }
  }
}
</script>

<style lang="scss">
  .tag-form {
    display: inline-block;

    &__input {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      font-size: 14px;
      padding: 3px 6px;
      border-radius: 3px;
      border: 0;
      outline: none;
      height: 20px;

      &:focus {
        border-color: #2c3e50;
      }
    }
  }
</style>
