<template>
  <div
    class="new-collection"
    :class="{'new-collection--active': visible}"
  >
    <div
      v-show="!visible"
      class="new-collection__message"
      @click="activate"
    >
      <icon name="tags" prefix="fas" />
      Neue Collection hinzufügen
    </div>
    <form
      v-if="visible"
      @submit.prevent="submit"
      class="new-collection__form"
    >
      <input
        ref="nameInput"
        class="new-collection__input"
        type="text"
        placeholder="Name ..."
        v-model="name"
      >

      <div class="new-collection__actions">
        <button
          class="button button--1 button--small"
          type="button"
          @click="submit"
        >
          Speichern
        </button>
        <button
          class="button button--2 button--small"
          type="button"
          @click="cancel"
        >
          Abbrechen
        </button>
      </div>
    </form>
  </div>


</template>

<script>
import { mapActions } from 'vuex'
import Icon from '@/components/Icon'

export default {
  components: {
    Icon
  },
  data () {
    return {
      visible: false,
      name: ''
    }
  },
  methods: {
    ...mapActions ({
      addCollection: 'collection/add'
    }),
    activate () {
      this.visible = true
    },
    submit () {
      this.addCollection(this.name)

      this.name = ''
      this.visible = false
    },
    cancel () {
      this.visible = false
    }
  }
}
</script>

