<template>
  <accordion-item refs="item">
    <accordion-header>
      {{ collection.name }}
      <span class="badge">
        {{ getTagsForCollection(collection.id).length }}
      </span>

      <div
        class="accordion__actions"
      >
        <div class="hidden">
          <tag-form
            :collection="collection"
          />
          <button
            class="button button--2 button--small"
            type="button"
            @click.stop="deleteCollection(collection)"
          >
            <icon name="trash-alt" />
            Löschen
          </button>
        </div>
        <button
          class="button button--2 button--small"
          type="button"
          @click.stop="selectAll(collection)"
        >
          <icon name="check-square" />
          Alle auswählen
        </button>
      </div>
    </accordion-header>
    <accordion-content>
      <div
        class="text-center msg"
        v-if="getTagsForCollection(collection.id).length === 0"
      >
        Noch keine Tags vorhanden
      </div>
      <tag
        v-for="tag in getTagsForCollection(collection.id)"
        :key="tag.id"
        :tag="tag"
      />
    </accordion-content>
  </accordion-item>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import Tag from '@/components/Tag'
import TagForm from '@/components/TagForm'
import AccordionItem from '@/components/AccordionItem'
import AccordionHeader from '@/components/AccordionHeader'
import AccordionContent from '@/components/AccordionContent'
import Icon from '@/components/Icon'

export default {
  props: {
    collection: {
      type: Object,
      required: true
    }
  },
  components: {
    Tag,
    TagForm,
    AccordionItem,
    AccordionHeader,
    AccordionContent,
    Icon,
  },
  computed: {
    ...mapState ('collection', {
      collections: state => state.items
    }),
    ...mapGetters ('tag', {
      getTagsForCollection: 'getTagsForCollection',
    })
  },
  methods: {
    ...mapActions ({
      deleteCollection: 'collection/remove',
      selectAll: 'tag/selectAll'
    })
  }
}
</script>
