import Vue from 'vue'
import Router from 'vue-router'
import Tags from './views/Tags.vue'
import Exif from './views/Exif.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Tags',
      component: Tags
    },
    {
      path: '/exif',
      name: 'Exif',
      component: Exif
    }
  ]
})
