import shortid from 'shortid'

export default {
  namespaced: true,

  state: {
    items: []
  },

  getters: {
    getTagsForCollection (state) {
      return id => {
          return state.items.filter(item => item.collectionId === id)
      }
    },
    selectedTags (state) {
      return state.items.filter(item => item.selected)
    }
  },

  mutations: {
    addItem (state, {collectionId, name}) {
      state.items.push({
        collectionId: collectionId,
        id: shortid.generate(),
        selected: false,
        name: name
      })
    },
    selectItem (state, id) {
      const item = state.items.find(item => item.id === id)
      item.selected = !item.selected
    },
    selectAll (state, items) {
      items.map(item => item.selected = true)
    },
    deleteItem (state, id) {
      const index = state.items.map(item => item.id).indexOf(id)
      state.items.splice(index, 1)
    },
    clearSelected (state) {
      state.items.filter(item => item.selected).map(item => item.selected = false)
    },
    emptyItems (state) {
      state.items = []
    }
  },

  actions: {
    add ({commit}, payload) {
      commit('addItem', payload)
    },
    select ({commit}, payload) {
      commit('selectItem', payload.id)
    },
    selectAll ({commit, getters}, payload)  {
      commit('selectAll', getters.getTagsForCollection(payload.id))
    },
    remove ({commit}, payload) {
      commit('deleteItem', payload.id)
    },
    clearSelected ({commit}) {
      commit('clearSelected')
    }
  }
}
