import shortid from 'shortid'

export default {
  namespaced: true,

  state: {
    items: []
  },

  getters: {
    sortedByName (state) {
      return state.items.sort(function(a,b) {return (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0);} )
    }
  },

  mutations: {
    addItem (state, name) {
      state.items.push({
        id: shortid.generate(),
        name: name
      })
    },
    removeItem (state, id) {
      const index = state.items.map(item => item.id).indexOf(id)
      state.items.splice(index, 1)
    },
    emptyItems (state) {
      state.items = []
    }
  },

  actions: {
    add ({commit}, name) {
      commit('addItem', name)
    },
    remove ({commit} , payload) {
      // commit('removeAllTags', payload.id)
      if (confirm("Delete collection?")) {
        commit('removeItem', payload.id)
      }
    }
  }
}
