<template>
  <div
    class="tag"
    @click="selectTag(tag)"
    @contextmenu.prevent="deleteTag(tag)"
    :class="{'tag__selected': tag.selected, 'tag__confirm': this.confirmDelete}"
  >
    #{{ tag.name }}
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  props: {
    tag: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      confirmDelete: false
    }
  },
  methods: {
    ...mapActions ({
      select: 'tag/select',
      remove: 'tag/remove'
    }),
    selectTag (tag) {
      if (this.confirmDelete) {
        this.remove(tag)
      } else {
        this.select(tag)
      }
    },
    deleteTag (tag) {
      if (!tag.selected) {
        this.confirmDelete = !this.confirmDelete
      }
    }
  }
}
</script>

<style lang="scss">
  .tag {
    background: #8492a0;
    color: #fff;
    display: inline-block;
    padding: 3px 10px;
    border-radius: 3px;
    cursor: pointer;
    user-select: none;
    margin: 3px 3px;
    font-size: 14px;

    &__selected {
      background: #42b983;
      background: #2c3e50;
    }

    &__confirm {
      background: orange;
    }
  }
</style>
