<template>
  <font-awesome-icon
    :icon="[prefix, name]"
  />
</template>

<script>
import { library } from '@fortawesome/fontawesome-svg-core'
import { faPencilAlt, faTags, faCameraRetro } from '@fortawesome/free-solid-svg-icons'
import { faCheckSquare, faTrashAlt, faClipboard } from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faClipboard, faCheckSquare, faTrashAlt, faPencilAlt, faTags, faCameraRetro)

export default {
  components: {
    FontAwesomeIcon
  },
  props: {
    name: {
      type: String,
      required: true
    },
    prefix: {
      type: String,
      default: 'far'
    }
  }
}
</script>
