<template>
  <div class="home">
    <div class="page-title">
      <h1>Hashtags</h1>
    </div>

    <div class="container">
      <div class="accordion">
        <collection
          v-for="collection in sortedCollections"
          :key="collection.id"
          :collection="collection"
        />
      </div>

      <collection-form />
    </div>

    <div class="spacer" :style="spacerStyles" />

    <div
      class="selected"
      ref="selected"
      v-show="selectedTags.length"
    >
      <div class="container">
        <h2>
          Tags
          <span
            :class="{'warning': selectedTags.length > 30}"
          >
            {{ selectedTags.length }} / 30 Tags ausgewählt
          </span>
        </h2>

        <div class="selected__actions">
          <button
            class="button button--3"
            type="button"
            @click="clearSelected"
          >
            <icon name="trash-alt" />
            zurücksetzen
          </button>

          <button
            class="button button--3"
            type="button"
            v-clipboard:copy="copyTags"
            @click="$popup({ message: 'kopiert' })"
          >
            <icon name="clipboard" />
            kopieren
          </button>
        </div>
        <tag
          v-for="tag in selectedTags"
          :key="tag.id"
          :tag="tag"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import Tag from '@/components/Tag'
import Collection from '@/components/Collection'
import CollectionForm from '@/components/CollectionForm'
import Icon from '@/components/Icon'

export default {
  name: 'home',
  components: {
    Tag,
    Collection,
    CollectionForm,
    Icon
  },
  data () {
    return {
      spacerHeight: 180
    }
  },
  computed: {
    ...mapState ('collection', {
      collections: state => state.items
    }),
    ...mapGetters ({
      selectedTags: 'tag/selectedTags',
      sortedCollections: 'collection/sortedByName'
    }),
    spacerStyles () {
      return {
        minHeight: `${this.spacerHeight}px`
      }
    },
    copyTags () {
      return this.selectedTags.map(tag => `#${tag.name}`).join(' ')
    }
  },
  methods: {
    ...mapActions ({
      clearSelected: 'tag/clearSelected',
    }),
    updateSpacerHeight () {
      this.spacerHeight = this.$refs.selected.clientHeight + 30
    }
  },
  mounted () {
    this.updateSpacerHeight()
  },
  updated () {
    this.updateSpacerHeight()
  }
}
</script>

<style lang="scss">
.warning {
  color: red;
}

.selected {
  position: fixed;
  border-top: 1px solid #2c3e50;
  bottom: 0;
  background: #42b983;
  left: 0;
  right: 0;
  padding: 20px 0;

  &__actions {
    position: absolute;
    top: 5px;
    right: 0;
  }
}
</style>
